'use strict';

// MODULES //

import setReadOnly from '@stdlib/utils-define-nonenumerable-read-only-property';
import setProps from '@stdlib/strided-base-meta-data-props';
import meta from './meta.json';
import types from './types.json';
import rsqrt from './rsqrt.js';
import ndarray from './ndarray.js';


// MAIN //

setReadOnly( rsqrt, 'ndarray', ndarray );
setProps( meta, types, rsqrt, false );
setProps( meta, types, rsqrt.ndarray, true );


// EXPORTS //

export default rsqrt;
